import React, {useState} from 'react'
import {ValidateAll} from "./Validations";
import {Button, Modal} from "react-bootstrap";
import $ from "jquery";



export function CreateAccountButton({setUser, navigate, setToken, url}) {
    const [showModal, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const CreateAccount = (async () => {
        let inputOK = ValidateAll(url);
        if (inputOK) {
            // when we validate the register fields are okay we send the details to the api
            const username = document.getElementById("user-name").value;
            const nickname = document.getElementById("display-name").value;
            const password = document.getElementById("password").value;
            const newUserJson = {
                "username": username,
                "password": password,
                "nickname": nickname,
                "server": "localhost:7225"
            }
            await $.ajax({
                url: url + 'api/users/register',
                type: 'POST',
                contentType: "application/json",
                data: JSON.stringify(newUserJson),
                success: function (data) {
                    setToken(data)
                    setUser(username)
                    navigate("/")
                },
            })
        } else {
            handleShow(true)
        }
    })
    return (
        <div>
            <button type="button" className="btn btn-secondary btn-lg btn-block" onClick={CreateAccount}>Create My
                Account
            </button>
            <div>
                <Modal show={showModal} onHide={handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Invalid Registration</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>Make sure your information is valid and try again</Modal.Body>
                    <Modal.Footer>
                        <Button variant="primary" onClick={handleClose}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>
            </div>
        </div>

    )
}
